<template>
  <div>
    <loading :active.sync="isLoading"></loading>
    <div class="container confirm_product my-3">
      <!-- banner -->
      <template>
        <h2 class="my-2 p-2">訂單明細</h2>
      </template>
      <div class="row d-flex">
        <div class="col-md-12 confirm_productinfo font-weight-normal d-flex">
          <!-- 商品資訊 -->
          <div class="col-md-8 d-flex justify-content-between">
            <div class="col-md-5" style="white-space: nowrap">
              <div
                class="d-flex mb-3 p-3"
                v-for="(item, index) in order.products"
                :key="index"
              >
                <div class="mr-3">
                  <img :src="item.product.imageUrl[0]" alt />
                  <!-- <router-link :to="`/product/${order.id}`"> -->
                </div>
                <div>
                  <div>{{ item.product.title }}</div>
                  <div>
                    單價:
                    <span class="text-danger">{{ item.product.price }}</span>
                  </div>
                  <div>數量: {{ item.quantity }}</div>
                  <div>小計: {{ item.product.price * item.quantity }} 元</div>
                </div>
              </div>
            </div>
          </div>
          <!-- 訂單資訊 -->
          <div class="col-md-4 confirm_userinfo">
            <!-- 小計 -->
            <div class="col-md-12 font-weight-bolder p-3"
            >
              <div class="d-flex justify-content-between">
                <span>小計</span>
                <span>{{ order.amount }} 元</span>
              </div>
              <div class="d-flex justify-content-between">
                <span>運費</span>
                <div>
                  <span v-if="order.amount >= 999">{{ 0 }} 元</span>
                  <span v-else>{{ 80 }} 元</span>
                </div>
              </div>
              <div class="d-flex justify-content-between my-3">
                <h5>訂單總計</h5>
                <div class="text-danger">
                  <h5 v-if="order.amount >= 999">{{ order.amount }} 元</h5>
                  <h5 v-else>{{ order.amount + 80 }} 元</h5>
                </div>
              </div>
            </div>
            <!-- 收件人資訊   -->
            <div class="p-3 mt-3">
              <div class="">
                <div class="d-flex justify-content-between">
                  <span>收件人:</span>
                  <span>{{ order.user.name }}</span>
                </div>
                <div class="d-flex justify-content-between">
                  <span>信箱:</span>
                  <span>{{ order.user.email }}</span>
                </div>
                <div class="d-flex justify-content-between">
                  <span>聯絡電話:</span>
                  <span>{{ order.user.tel }} </span>
                </div>
                <div class="d-flex justify-content-between">
                  <span>地址:</span>
                  <span>{{ order.user.address }}</span>
                </div>
              </div>
              <div class="">
                <div class="d-flex justify-content-between">
                  <span>付款方式:</span>
                  <span>{{ order.payment }}</span>
                </div>
                <div class="d-flex justify-content-between">
                  <span>訂單建立時間:</span>
                  <span>{{ order.updated.datetime }}</span></div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="row d-flex justify-content-center my-3">
        <button style="button" class="btn btn-dark">
              <router-link class=""  to="/products">繼續購物</router-link>
        </button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Confirm',
  data () {
    return {
      isLoading: false,
      fullPage: true,
      order: {
        user: {}
      },
      orderId: '',
      isEnabled: false,
      quantityTatol: 0,
      uuid: process.env.VUE_APP_UUID
    }
  },
  created () {
    this.orderId = this.$route.params.orderId
    this.getOrder()
  },
  methods: {
    getOrder () {
      this.isLoading = true
      const url = `${process.env.VUE_APP_APIPATH}/api/${this.uuid}/ec/orders/${this.orderId}`
      this.$http
        .get(url)
        .then((res) => {
          this.order = res.data.data
          if (this.order.coupon === null) {
            this.isEnabled = false
          } else {
            this.isEnabled = true
          }
          this.calTotal()
          this.isLoading = false
        })
        .catch((err) => {
          this.isLoading = false
          this.$bus.$emit(
            'message:push',
            `資料讀取失敗，請稍後再試
            ${err}`,
            'danger'
          )
        })
    },
    // 計算商品總件數及總價
    calTotal () {
      let sumQuantity = 0
      this.order.products.forEach((item) => {
        sumQuantity += item.quantity
      })
      this.quantityTatol = sumQuantity
    },
    payOrder () {
      this.isLoading = true
      const url = `${process.env.VUE_APP_APIPATH}/api/${this.uuid}/ec/orders/${this.orderId}/paying`
      this.$http
        .post(url)
        .then((res) => {
          if (res.data.data.paid) {
            this.getOrder()
            this.$bus.$emit('message:push', '付款成功', 'success')
          }
          this.isProcessing = false
        })
        .catch((err) => {
          this.$bus.$emit(
            'message:push',
            `付款失敗，請稍後再試
            ${err}`,
            'danger'
          )
          this.isProcessing = false
        })
    }
  }
}
</script>
